*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.content{
    min-height: calc(100vh- 70px - 64px);
    padding: 1.5rem 0;
}

.list{
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(250px,1fr));
    gap: 1rem;
}

.card{
    display: flex;
    flex-direction: column;
}

.card-content{
    flex-grow: 1;
}

img{
    max-width: 100%;
    object-fit: cover;
    margin-top: 1rem;
}
